body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
}

.cube-face {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 4px;
}

.face-0 {
  grid-area: front;
}

.face-1 {
  grid-area: left;
}

.face-2 {
  grid-area: right;
}

.face-3 {
  grid-area: back;
  aspect-ratio: 3/3;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-auto-flow: dense;
}

.face-4 {
  grid-area: top;
}

.face-5 {
  grid-area: bottom;
}

.single-square {
  border: 4px outset white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0;
  aspect-ratio: 1;
}

.color-0 {
  background-color: #2ecc71;
  border-color: #28b463;
}

.color-1 {
  background-color: #e67e22;
  border-color: #ca6f1e;
}

.color-2 {
  background-color: #c0392b;
  border-color: #a93226;
}

.color-3 {
  background-color: #3498db;
  border-color: #2e86c1;
}

.color-4 {
  background-color: #ecf0f1;
  border-color: #d0d3d4;
}

.color-5 {
  background-color: #f1c40f;
  border-color: #d4ac0d;
}

/* Renders Back face in opposite way */
.face-3 > .single-square:nth-child(3n + 1) {
  grid-column: 3;
}

.face-3 > .single-square:nth-child(3n + 2) {
  grid-column: 2;
}

@media screen and (max-width: 575.98px) {
  /* Renders Back face columms in direct way */
  .face-3 > .single-square:nth-child(3n + 1) {
    grid-column: 1;
  }

  .face-3 > .single-square:nth-child(3n + 2) {
    grid-column: 2;
  }

  .face-3 > .single-square:nth-child(-n + 3) {
    grid-row: 3;
  }

  .face-3 > .single-square:nth-last-child(-n + 3) {
    grid-row: 1;
  }
}

.opened-cube {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-template-areas:
    ". top . ."
    "left front right back"
    ". bottom . .";
  grid-gap: 3%;
  padding-left: 5%;
  padding-right: 5%;
}

@media screen and (max-width: 575px) {
  .opened-cube {
    aspect-ratio: 3/4;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-template-areas:
      ". top ."
      "left front right"
      ". bottom ."
      ". back .";
  }
}

.App {
  text-align: center;
  margin: auto;
  background-color: #282c34;
  height: 100vh;
  aspect-ratio: 9/16;
  display: grid;
  grid-template-rows: 10% 1fr 1fr 10%;
  grid-template-columns: 1fr;
  grid-row-gap: 10px;
  font-size: larger;
  color: white;
}

.cube3d {
  width: 100%;
  height: 100%;
  justify-self: center;
}

.App-link {
  color: #61dafb;
}

.controls {
  padding: 8px;
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
}

.move-buttons {
  height: 100%;
  display: grid;
  align-items: center;
  justify-content: center;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(6, 1fr);
  grid-row: 1 / span 2;
  grid-column: 2;
}

button.reset-button {
  grid-row: 1 / span 2;
  grid-column: 1;
}
button.undo-button {
}
button.redo-button {
  grid-row: 2;
  grid-column: 3;
}

button.move-button {
  height: 100%;
  justify-content: center;
  font-size: large;
}

button.move-button:hover {
  border-color: #b3b6b7;
  background-color: #d0d3d4;
}

button.move-button:focus {
  border-color: #979a9a;
  background-color: #b3b6b7;
}

button.move-button:active {
  border-color: #7b7d7d;
  background-color: #979a9a;
}

@media screen and (max-width: 575px) {
  .App {
    max-height: 100vh;
    aspect-ratio: 9/16;
  }
}

